@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  background-color: #3f3f3f;
}

.expenses {
  background-color: #2a2a2a;
  max-width: 50rem;
  margin: 2rem auto;
  padding: 1rem;
}

.card {
  border-radius: 1.2rem;
  box-shadow: 0 0.1rem 0.8rem rgba(0, 0, 0, 0.25);
}

.expense-filter {
  color: #fff;
  padding: 0 1rem;
}

.expense-filter__control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
}

.expense-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expense-filter select {
  font: inherit;
  font-weight: bold;
  padding: 0.5rem 3rem;
  border-radius: 0.6rem;
}

.expenses-list {
  list-style: none;
  padding: 0;
}

.expenses-list__fallback  {
  color: #fff;
  text-align: center;
}

.expense-date {
  background-color: #2a2a2a;
  color: #fff;
  border: 0.1rem solid #ececec;
  border-radius: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.75rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item h2 {
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: #fff;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #40005d;
  border: 0.1rem solid #fff;
  padding: 0.5rem;
  border-radius: 1.2rem;
}

.expense-item__x-button {
  background-color: transparent;
  border: none;
}

.expense-item__x-button Svg {
  fill: #fff;
  background-color: red;
  border-radius: .3rem;
}

@media (min-width: 36.25rem) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}

.chart {
  padding: 1rem;
  border-radius: 12px;
  background-color: #f8dfff;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
}

.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}

.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-expense__control {
  /* flex: 1 1 0; */
}

.new-expense__control label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 0.6rem;
  border: 0.1rem solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-expense__actions {
  text-align: right;
}

.new-expense {
  background-color: #a892ee;
  padding: 1rem;
  margin: 2rem auto;
  max-width: 50rem;
  border-radius: 1.2rem;
  text-align: center;
  box-shadow: 0 0.1rem 0.8rem rgba(0, 0, 0, 0.25);
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 0.1rem solid #40005d;
  background-color: #40005d;
  color: #fff;
  border-radius: 1.2rem;
  margin-left: 1rem;
}

.new-expense button:hover,
.new-expense button:active {
  background-color: #510674;
  border-color: #510674;
}

.new-expense button.alternative {
  background-color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: #ddb3f8;
}

