.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-expense__control {
  /* flex: 1 1 0; */
}

.new-expense__control label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 0.6rem;
  border: 0.1rem solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-expense__actions {
  text-align: right;
}
